<template>
  <div id="userList" class="chat-list bg-secondary p-0" :style="`overflow:hidden auto`" :class="{'hide-user col-8': isMobile}">
    <div class="container-fluid p-0">
      <div class="list-group p-0 dropdown" style="min-width: 200px" v-for="user in groupUserList" :key="user.id" >
        <a class="list-group-item list-group-item-action p-1 m-1 rounded text-white" 
            role="button" :id="user._id" data-toggle="dropdown" aria-haspopup="false" aria-expanded="false">
          <img :src="user.imgURL" style="height: 30px;width: 30px" class="rounded-circle">
          {{user.name}}
        </a>
        <div class="dropdown-menu" :aria-labelledby="user._id">
          <a class="dropdown-item" @click="createConversation(user)" v-if="user._id != userInfo._id">Message</a>
          <a class="dropdown-item" v-if="user._id != userInfo._id && !userInfo.blockedUser.includes(user._id)" @click="blockUser(user._id)" data-toggle="snackbar" :data-content="`Blocked ${user.name}`" data-html-allowed="true" data-timeout="0">Block</a>
          <a class="dropdown-item" v-if="user._id != userInfo._id && userInfo.blockedUser.includes(user._id)" @click="unblockUser(user._id)" data-toggle="snackbar" :data-content="`Unblocked ${user.name}`" data-html-allowed="true" data-timeout="0">Unblock</a>
          <router-link to="/profile" class="dropdown-item" v-if="user._id == userInfo._id">Profile</router-link>
          <router-link :to="'/profile/' + user._id" class="dropdown-item" v-if="user._id != userInfo._id">Profile</router-link>
          <a class="dropdown-item" v-if="userInfo._id == group[0].ownerId && user._id != userInfo._id" @click="removeFromGroup(user._id)">Remove from group</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { isMobile, swipedetect } from "../utilities";

export default {
  name: "UserList",
  computed: {
    ...mapState({
      unsub: state => state.postView.unsubPost,
      post: state => state.postView.post,
      userInfo: state => state.auth.userInfo,
      getUserStatus: state => state.auth.userInfo.status,
      getUsername: state => state.auth.userInfo.name,
      notify: state => (state.auth.notify ? state.auth.notify : []),
      inactiveNotify: state =>
        state.auth.inactiveNoti ? state.auth.inactiveNoti : [],
      chatList: state => state.chat.chatList,
      pendingRequests: state => state.auth.pendingRequests,
      friendNotify: state => state.auth.friendNotify,
      converList: state => state.chat.converList,
      joinedGroupList: state => state.group.joinedGroupList,
      groupUserList: state => state.group.groupUserList
    })
  },
  data() {
    return {
      groupId: "",
      groupChatId: "",
      route: "",
      isMobile: isMobile.any(),
      wh: window.innerHeight + 'px',
      group: ''
    };
  },
  props: {
    userList: Array
  },
  watch: {
    userInfo(n, o) {
      this.$store.dispatch("getAllUserWithConversation", this.userInfo.id);
      this.$store.dispatch("getAllJoinedGroup", this.userInfo._id);
    },
    $route(n, o) {
      this.groupId = n.params.id;
      this.route = n.name;
      this.group = this.joinedGroupList.filter(g => g.groupId == this.$route.params.id);
      this.$store.dispatch('getAllGroupUser', this.group[0].membersId);
    },
    converList(n) {
      // this.$store.dispatch("getAllUserWithConversation", this.userInfo.id);
    },
    // joinedGroupList(n) {
    //   if (this.$route.name == "group") {
    //     this.$router.push("/group/" + n[n.length - 1].groupId);
    //   }
    // }
  },
  beforeMount() {
    // this.$store.dispatch("getAllUserWithConversation", this.userInfo.id);
  },
  created() {
    this.group = this.joinedGroupList.filter(g => g.groupId == this.$route.params.id);
    this.$store.dispatch('getAllGroupUser', this.group[0].membersId);
    console.log(this.group, this.groupUserList);
  },
  mounted() {
    $("#listChat").bootstrapMaterialDesign();
    swipedetect(document.getElementById('userList'), dir => {
      if (dir == "right") {
        document.getElementById('userList').style.right = "-100%"
      }
    })
  },
  methods: {
    countNew: function(c) {
      let isNew = true,
        count = 0;
      for (let i = c.content.length - 1; i >= 0; i--) {
        if (c.content[i].owner != this.userInfo.name && isNew) {
          count++;
        }
      }
      return count;
    },
    openConversation: function(c) {},
    openMenu: function() {
      if (document.getElementById("userList").style.right == "-100%") {
        document.getElementById("menu-overlay").classList.add('menu-overlay');
        document.getElementById("userList").style.right = "0";
      } else {
        document.getElementById("menu-overlay").classList.remove('menu-overlay');
        document.getElementById("userList").style.right = "-100%";
      }
    },
    createConversation: function(u) {
      this.$store.dispatch("CREATE_CONVERSATION", {
        u,
        userInfo: this.userInfo
      }).then(() => {
        this.chatList.forEach(c => {
          let id = u.id + this.userInfo.id;
          if (c.membersId.indexOf(id) >= 0) {
            $('a[href="#/personal"]').click();
            this.$router.push('/personal/' + c.groupId)
          }
        })
      });
      // this.$store.dispatch("GET_CONVERSATION", this.userInfo.id + u.id);
    },
    blockUser(id) {
      this.$store.dispatch("BLOCK_USER", {
        id: this.userInfo.id,
        userId: id
      });
    },
    unblockUser(id) {
      this.$store.dispatch("UNBLOCK_USER", { id: this.userInfo.id, userId: id });
    },
    removeFromGroup(id) {
      this.$store.dispatch("removeFromGroup", { id: id, groupId: this.group[0].groupId});
    }
  }
};
</script>

<style scoped>
.list-group-item.d-flex.justify-content-between.align-items-center:hover {
  background-color: rgba(0, 0, 0, 0.1);
  cursor: pointer;
}
a {
  text-decoration: none;
}
.bg-secondary.text-white {
  border-left: 7px solid var(--primary);
}
.hide-user {
  right: -100%;
  position: fixed;
  top: 0;
  z-index: 9999;
  top: 56px;
  height: 100%;
}
.chat-list {
  transition: all .4s ease-in-out;
}
.dropdown-item {
  cursor: pointer;
}
.list-group-item-action:hover {
  background-color: rgba(0, 0, 0, 0.2)
}
</style>



