<template>
  <div class="container-fluid main p-0" :class="[isMobile ? 'full-size' : 'row m-0']">
    <div
      class="d-flex align-items-end card border-0 p-0"
      style="height: 100%"
      :class="[isMobile ? '' : 'col-10']"
    >
      <div class="container-fluid card-body p-0" style="height: calc(100vh - 56px - 64px)">
        <ul
          id="group-messages"
          class="list-group list-group-flush d-flex flex-column-reverse main-message"
          style="overflow-x:hidden;padding-bottom: 20px;"
          @click="closeUserList()"
        >
          <a
            class="list-group-item px-4 py-1"
            v-for="(c, i) in newMessages"
            :key="c.createdTime"
            style="border: none;"
          >
            <ChatMessage :c="c" :next="newMessages[i+1]"/>
          </a>
          <hr class="m-1" v-if="!oldMessages">
          <a
            class="list-group-item px-4 py-1"
            v-for="(c, i) in oldMessages"
            :key="c.createdTime"
            style="border: none;"
          >
            <ChatMessage :c="c" :next="oldMessages[i+1]"/>
          </a>
        </ul>
      </div>
      <div class="container-fluid card-footer" style="position: relative">
        <div class="images-preview d-flex justify-content-center bg-light p-2" v-if="images.length">
          <div
            class="image_preview border mx-1"
            v-for="(i, index) in images"
            :key="index"
            :style="'background-image: url('+ i +')'"
          >
            <i
              class="fas fa-lg fa-times-circle bg-white rounded-circle text-dark"
              style="position: absolute; top: 5px;right: 5px;cursor: pointer;display:none"
              @click="removeImage(index)"
            ></i>
          </div>
        </div>

        <div class="input-group">
          <div class="input-group-prepend border">
            <label class="btn btn-primary m-0">
              <i class="far fa-plus-square fa-lg fa-2x" style="vertical-align: top;"></i>
              <input
                type="file"
                id="inputImage"
                multiple
                style="visibility: hidden;display: none"
                @change="chooseImage($event)"
              >
            </label>
          </div>

          <div
            contenteditable="true"
            class="p-2 border-top message form-control"
            style="max-height: 42px; overflow-y:auto"
            id="comment"
            :class="{ minimize: minimize }"
            @keyup.exact="searchEmoji($event)"
            @keyup.exact.enter="sendMessage($event)"
            @paste="pasteImage($event)"
          ></div>

          <div class="input-group-append border">
            <button class="btn btn-primary m-0" type="button" @click="emojiPicker = !emojiPicker;">
              <img
                src="https://twemoji.maxcdn.com/2/72x72/1f600.png"
                class="emoji"
                style="height: 1.5em"
              >
            </button>
            <button class="btn btn-danger m-0" type="button" @click="sendMessage($event);">
              <i class="fas fa-reply"></i>
            </button>
          </div>
        </div>
      </div>
    </div>
    <UserList :class="[isMobile ? 'p-0' : 'col-2 p-0']"/>
    <div class="emoji-picker" v-show="emojiPicker">
      <Picker
        :set="'twitter'"
        :native="false"
        :emojiTooltip="!isMobile"
        :showPreview="!isMobile"
        :style="{
						position: 'absolute',
						bottom: isMobile ? '68px' :'124px',
						right: isMobile ? '0' :'17%',
						width: isMobile ? '100vw' : '50vw' ,
						zIndex: 300,
					}"
        @select="selectEmoji($event);"
      />
    </div>
    <!-- <v-dialog /> -->
    <!-- <modal name="view-post" :reset="true" :adaptive="true" :delay="100"></modal> -->
  </div>
</template>

<script>
import { Picker, emojiIndex } from "emoji-mart-vue";
import moment from "moment";
import db from "../database/firestore";
import query from "../database/db.actions";
import {
  GET_CONVERSATION,
  SEND_MESSAGE,
  UPLOAD_IMAGE_TO_CHAT,
  REMOVE_UPLOAD_IMAGE,
  FILE_UPLOAD,
  REMOVE_UPLOAD_FILE
} from "../store/actions.type";

import { mapState } from "vuex";
import { convertDate, isMobile, swipedetect } from "../utilities/index";

import ChatMessage from "./ChatMessage";
import UserList from "./UserList";

export default {
  name: "Group",
  components: {
    Picker,
    ChatMessage,
    UserList
  },
  props: ["id"],
  data() {
    return {
      isMobile: isMobile.any(),
      messages: {},
      groupId: this.$route.params.id,
      chat: [],
      minimize: false,
      isFocus: true,
      message: "",
      newMessages: [],
      oldMessages: [],
      emojiPicker: false,
      emojiRegex: /(:[^\s]*(?:::[^:\s]*)*[^:\s]:)|(:[^\s]*(?:::[^:\s]*)*[^:\s])/g,
      emojiRegex2: /:\w{2,}:/g,
      matchesEmoji: [],
      emo: "",
      images: [], // url images
      files: [], // url file
      canSendMessage: true,
      hasImage: false,
      hasFile: false,
      paths: [], // img path "_id/time_image_name"
      filePaths: [], // file path
      realImage: [], // image file object
      realFile: [], // file object
      imgURL: [],
      filelimit: 20,
      converlength: 0,
      isScrollable: false,
      now: 0,
      limitMessage: 30,
      messageLimit: 1000,
      now: Date.now()
    };
  },
  computed: {
    ...mapState({
      userInfo: state => state.auth.userInfo,
      getUserStatus: state => state.auth.userInfo.status,
      getUsername: state => state.auth.userInfo.name,
      notify: state => (state.auth.notify ? state.auth.notify : []),
      inactiveNotify: state =>
        state.auth.inactiveNoti ? state.auth.inactiveNoti : [],
      joinedGroupList: state => state.group.joinedGroupList,
      pendingRequests: state => state.auth.pendingRequests,
      friendNotify: state => state.auth.friendNotify,
      groupMessage: state => state.group.groupMessage,
      imgURLs: state => state.chat.imgURL
    })
  },
  watch: {
    id() {
      this.messages = this.joinedGroupList.filter(e => e.groupId == this.id)[0] || {};
      console.log();
    },
    $route(n) {
      let groupId = this.$route.params.id;
      this.groupId = groupId;
      this.getOldMessage(groupId);
      this.getNewMessage(groupId);
    },
    imgURLs(n, o) {
      console.log(n, o)
      if (n.length == this.images.length) {
        this.send();
      }
    }
  },
  methods: {
    calendarTime: convertDate,
    selectEmoji(emoji) {
      document.getElementById("comment").innerHTML +=
        twemoji.parse(twemoji.convert.fromCodePoint(emoji.unified)) +
        '<div style="display: none">' +
        emoji.colons +
        "</div>";
      this.emojiPicker = false;
      this.setCaret();
    },
    searchEmoji(e) {
      this.message = document.getElementById("comment").innerText;
      if ((this.message.length > 2 && e.keyCode == 32) || e.keyCode == 13) {
        this.emo = this.message.match(this.emojiRegex);
        let a = this.emo ? this.emo[this.emo.length - 1].substr(1) : "";
        if (a.length > 1) {
          this.matchesEmoji = emojiIndex.search(a).map(o => o);
          this.replaceEmoji();
        }
      }
    },
    replaceEmoji(emoji) {
      let input = document.getElementById("comment");
      if (emoji) {
        this.message = input.innerHTML;
        let a = twemoji.parse(twemoji.convert.fromCodePoint(emoji.unified));
        input.innerHTML = this.message.replace(
          this.emo,
          a + '<div style="display: none">' + emoji.colons + "</div>"
        );
        this.setCaret();
        this.matchesEmoji = [];
        this.emo = [];
      } else if (this.matchesEmoji.length) {
        this.message = input.innerHTML;
        let a = twemoji.parse(
          twemoji.convert.fromCodePoint(this.matchesEmoji[0].unified)
        );
        input.innerHTML = this.message.replace(
          this.emo,
          a +
            '<div style="display: none">' +
            this.matchesEmoji[0].colons +
            "</div>"
        );
        this.setCaret();
        this.matchesEmoji = [];
        this.emo = [];
      }
    },
    onDragover() {
      // this.isDragging = true;
      $("#dropzonebg").addClass("show");
      console.log(1);
      return false;
    },
    onDragend() {
      // this.isDragging = false;
      $("#dropzonebg").removeClass("show");

      console.log(2);

      return false;
    },
    onDrop(e) {
      // this.isDragging = false;
      $("#dropzonebg").removeClass("show");
      console.log(e);
      e.preventDefault();

      var file = e.originalEvent.dataTransfer.files[0],
        reader = new FileReader();

      reader.onload = function(event) {
        // $dropimg.css("background-image", "url(" + event.target.result + ")");
        console.log(event.target.result);
      };

      console.log(file);
      reader.readAsDataURL(file);

      return false;
    },
    chooseImage(e) {
      let file = $("#inputImage").get(0).files,
        vm = this,
        oversize = [];
      // if (file.length >= this.filelimit || this.images.length >= this.filelimit) {
      //   vm.oversize(
      //     `LIMIT IMAGE IS ${filelimit}`,
      //     `Limit images per upload is ${filelimit}`
      //   );
      // } else
      for (let f of file) {
        if (f.size < 5000000) {
          if (
            f.type == "image/png" ||
            f.type == "image/jpeg" ||
            f.type == "image/gif"
          ) {
            let reader = new FileReader();
            this.realImage.push(f);
            this.images.push(window.URL.createObjectURL(f));
            let path =
              this.messages.groupId + "/" + Date.now() + "_" + file[0].name;
            this.paths.push(path);
            // reader.onload = function(event) {
            // vm.images.push(event.target.result);
            // };
            // reader.readAsDataURL(f);
          } else {
          }
        } else {
          oversize.push(f.name);
        }
      }
      if (oversize.length) {
        vm.oversize(
          "YOUR IMAGE IS TO BIG",
          `Try other image or reduce image size.\n 
          <p>Images: <code>${oversize}</code></p>`
        );
      }
    },
    dropImage(e) {
      e.preventDefault();
      e.stopPropagation();
      let dt = e.dataTransfer;
      let files = dt.files;
      if (files.length) {
        if (files.length >= this.limitFiles)
          alert("Limit" + this.limitFiles + "files");
        for (let i = 0; i < files.length; i++) {
          if (files[i].type != "image/jpeg" && files[i].type != "image/png") {
            let path =
              this.messages.groupId + "/" + Date.now() + "_" + files[i].name;
            this.canSendMessage = false;
            this.hasFile = true;
            this.filePaths.push(path);
            this.realFile.push(files[i]);
            // this.uploadFile(files[i], path, id);
          } else if (
            files[i].type == "image/jpeg" ||
            files[i].type == "image/png"
          ) {
            this.canSendMessage = false;
            this.hasImage = true;
            let path =
              this.messages.groupId + "/" + Date.now() + "_" + files[i].name;
            this.paths.push(path);
            this.images.push(window.URL.createObjectURL(files[i]));
            this.realImage.push(files[i]);
            // this.uploadImage(files[i], path, id);
          }
        }
      }
      this.dragLeave(e);
    },
    uploadFile: async function(file, path, id) {
      this.$store.dispatch(FILE_UPLOAD, {
        file: file,
        path: path,
        id: id
      });
      // let interval = setInterval(() => {
      this.files = await this.$store.state.chat.files[id];
      let count = await this.$store.state.chat.fileCount;
      // if (this.files.toString() == this.filePaths.toString() && count == this.filePaths.length) {
      // 	// clearInterval(interval);
      // 	this.canSendMessage = true;
      // }
      console.log(this.files);
      // }, 200);
    },
    pasteImage(e) {
      let id = this.messages.groupId;
      e.preventDefault();
      let file = e.clipboardData.files;
      if (file.length) {
        let path = id + "/" + Date.now() + "_" + file[0].name;
        this.paths.push(path);
        this.images.push(window.URL.createObjectURL(file[0]));
        this.realImage.push(file[0]);
      }
      let text = (e.originalEvent || e).clipboardData.getData("text/plain");
      document.execCommand("insertHTML", false, text);

      // let id = this.messages.id;
      // let file = e.clipboardData.files;
      // if (file.length) {
      //   if (this.images.length > 7) {
      //     return;
      //   }
      //   // this.canSendMessage = false;
      //   this.hasImage = true;
      //   let path = this.messages.groupId + "/" + Date.now() + file[0].name;
      //   this.paths.push(path);
      //   this.images.push(window.URL.createObjectURL(file[0]));
      //   this.realImage.push(file[0]);
      //   this.$forceUpdate();
      //   // this.uploadImage(file[0], path, id);
      // } else {
      //   return;
      // }
      // e.preventDefault();
      // let text = (e.originalEvent || e).clipboardData.getData("text/plain");
      // document.execCommand("insertHTML", false, text);
    },
    uploadImage: async function(file, path, id) {
      // this.canSendMessage = false;

      this.$store.dispatch(UPLOAD_IMAGE_TO_CHAT, {
        file: file,
        path: path,
        id: id
      });
      // let interval = setInterval(() => {
      let count = this.$store.state.chat.fileCount;
      this.imgURL = await this.$store.state.chat.images[id];
      // if (this.images.toString() == this.paths.toString() && count == this.paths.length) {
      // 	// clearInterval(interval);
      // 	this.canSendMessage = true;
      // }
      console.log(this.imgURL);
      // }, 200);
    },
    removeImage(k) {
      // this.$store.dispatch(REMOVE_UPLOAD_IMAGE, {
      // 	amount: 1,
      // 	index: k,
      // 	path: this.paths[k],
      // 	id: this.messages.id
      // });
      this.images.splice(k, 1);
      this.realImage.splice(k, 1);
      window.URL.revokeObjectURL(this.realImage.splice(k, 1));
      this.paths.splice(k, 1);
      if (this.images.length == 0) this.hasImage = false;
    },
    removeFile(k) {
      // this.$store.dispatch(REMOVE_UPLOAD_FILE, {
      // 	amount: 1,
      // 	index: k,
      // 	path: this.filePaths[k],
      // 	id: this.messages.id
      // });
      // this.files.splice(k, 1);
      this.filePaths.splice(k, 1);
      this.realFile.splice(k, 1);
      if (this.files.length == 0) this.hasFile = false;
    },
    oversize(title, text) {
      this.$modal.show("dialog", {
        title,
        text,
        buttons: [
          {
            title: "CLOSE",
            default: false
          }
        ]
      });
    },
    send() {
      this.message = document.getElementById("comment").textContent;
      if (this.message.length > this.messageLimit) {
        vm.oversize(
          "YOUR MESSAGE IS TOO LONG",
          "Please make your message shorter. Limit is 1000 characters"
        );
      } else {
        if (this.message.length == 0 && this.imgURL.length == 0) {
          return;
        } else {
          let m = {
            message: this.message,
            owner: this.userInfo.name,
            ownerId: this.userInfo._id,
            _id: this.messages.id,
            userImg: this.userInfo.imgURL,
            img: this.imgURLs,
            file: this.files,
            groupId: this.messages.groupId
          };
          this.$store.dispatch(SEND_MESSAGE, m).then(() => {
            this.$store.dispatch("resetImage");
            document.getElementById("comment").innerHTML = "";
            this.images = [];
            this.files = [];
            this.paths = [];
            this.filePaths = [];
            this.hasImage = false;
            this.hasFile = false;
            this.realFile = [];
            this.realImage = [];
            this.imgURL = [];
          });
        }
      }
    },
    sendMessage(e) {
      this.$store.dispatch('resetImage')
      let vm = this;
      e.preventDefault();
      let id = this.messages.groupId;
      let retry = 0;
      if (this.emo) this.searchEmoji(e);

      if (this.hasFile) {
        let fileLength = this.filePaths.length - 1;
        for (let i = 0; i <= fileLength; i++) {
          this.uploadFile(this.realFile[i], this.filePaths[i], id);
        }
      }

      if (this.realImage.length) {
        let imageLength = this.paths.length;
        for (let i = 0; i < imageLength; i++) {
          this.uploadImage(this.realImage[i], this.paths[i], id);
        }
      }
      return;

      this.message = document.getElementById("comment").textContent;
      if (this.message.length > this.messageLimit) {
        vm.oversize(
          "YOUR MESSAGE IS TOO LONG",
          "Please make your message shorter. Limit is 1000 characters"
        );
      } else {
        let vm = this;
        let d = setInterval(() => {
          if (
            (this.message.length ||
              this.imgURL.length == this.images.length ||
              this.files.length) &&
            this.canSendMessage
          ) {
            let m = {
              // membersId: this.messages.membersId[0],
              message: this.message,
              owner: this.userInfo.name,
              ownerId: this.userInfo._id,
              _id: this.messages.id,
              userImg: this.userInfo.imgURL,
              img: this.imgURL,
              file: this.files,
              groupId: this.messages.groupId
            };
            this.$store.dispatch(SEND_MESSAGE, m).then(this.scrollChat());
            document.getElementById("comment").innerHTML = "";
            this.images = [];
            this.files = [];
            this.paths = [];
            this.filePaths = [];
            this.hasImage = false;
            this.hasFile = false;
            this.realFile = [];
            this.realImage = [];
            this.imgURL = [];
            setTimeout(() => {
              console.log(this.$store.state.chat.newMessage);
              this.$forceUpdate();
              this.scrollChat();
            }, 10);
            this.newMessages = Object.assign(
              this.newMessages,
              this.$store.state.chat.newMessage
            );
            clearInterval(d);
            // if (!this.paths.length) {
            // 	this.$store.dispatch(REMOVE_UPLOAD_IMAGE, {
            // 		amount: 0,
            // 		id: this.messages.id
            // 	});
            // }
            // if (!this.filePaths.length) {
            // 	this.$store.dispatch(REMOVE_UPLOAD_FILE, {
            // 		amount: 0,
            // 		id: this.messages.id
            // 	});
            // }
            // clearInterval(interval);
          } else {
            console.log("Cant send messsage");
          }
          retry++;
          if (retry == 100) {
            clearInterval(d);
            console.log("Timeout");
          }
        }, 1000);
        // }, 200);
      }
    },
    loadOldMessage(e) {
      let scrollTop = e.target.scrollTop,
        groupId = this.messages.groupId;

      if (
        scrollTop < 100 &&
        Date.now() - this.now > 2000 &&
        this.isScrollable
      ) {
        this.now = Date.now();
        this.isScrollable = false;
        this.limitMessage += 10;
        this.getOldMessage(groupId);
      }
    },
    scrollChat() {
      this.isScrollable = false;
      window.setTimeout(() => {
        let el = document.getElementById("main-messages");
        if (el) el.scrollTop = el.scrollHeight;
        this.isScrollable = true;
      }, 100);
    },
    setCaret() {
      setTimeout(() => {
        var el = document.getElementById("comment");
        var range = document.createRange();
        var sel = window.getSelection();
        if (!el || el.innerHTML == "") return;
        else
          range.setStart(
            el.childNodes[el.childNodes.length - 1],
            el.childNodes[el.childNodes.length - 1].length
          );
        range.collapse(true);
        sel.removeAllRanges();
        sel.addRange(range);
        el.focus();
      }, 200);
    },
    dragEnter(e) {
      document.getElementById("dropzone").classList.add("border-primary");
    },
    dragLeave(e) {
      document.getElementById("dropzone").classList.remove("border-primary");
    },
    checkImageLink(link) {
      let a = link.indexOf(".jpg");
      let b = link.indexOf(".png");
      let c = link.indexOf(".jpeg");
      if (a > -1 || b > -1 || c > -1) {
        return true;
      } else return false;
    },
    getOldMessage(groupId) {
      db.collection("message")
        .where("groupId", "==", groupId)
        .where("createdTime", "<", this.now)
        .orderBy("createdTime", "desc")
        .limit(this.limitMessage)
        .get()
        .then(querySnapshot => {
          let a = [];
          console.log(querySnapshot);
          if (querySnapshot.empty) {
            console.log({ empty: true });
            this.oldMessages = [];
          } else {
            querySnapshot.forEach(doc => {
              a.push(doc.data());
            });
            console.log(a);
            this.oldMessages = a;
          }
        });
      this.isScrollable = true;
    },
    getNewMessage(groupId) {
      this.unsub = db
        .collection("message")
        .where("groupId", "==", groupId)
        .where("createdTime", ">", this.now)
        .orderBy("createdTime", "desc")
        .onSnapshot(querySnapshot => {
          let a = [];
          console.log(querySnapshot);
          if (querySnapshot.empty) {
            console.log({ empty: true });
            this.newMessages = [];
          } else {
            querySnapshot.forEach(doc => {
              a.push(doc.data());
            });
            console.log(a);
            this.newMessages = a;
            this.scrollChat();
          }
        });
    },
    closeUserList() {
      if (this.isMobile && document.getElementById("userList"))
        if ($("#userList").css("right") == "0px")
          document.getElementById("userList").style.right = "-100%";
    }
  },
  mounted: function() {
    let groupId = this.$route.params.id;
    console.log(groupId);
    this.getOldMessage(groupId);
    this.getNewMessage(groupId);
    // this.$store.dispatch("GET_NEW_MESSAGE", { groupId, time: Date.now() });
    // this.$store.dispatch("GET_NEW_MESSAGE", { groupId, time: Date.now(), limit: 50 });
    let vm = this;
    setTimeout(() => {
      vm.scrollChat();
    }, 100);
    swipedetect(document.getElementById("group-messages"), dir => {
      if (dir == "right") {
        if (
          document.getElementById("listChat").style.left != "0px" &&
          document.getElementById("userList") &&
          $("#userList").css("right") != "0px"
        ) {
          document.getElementById("listChat").style.left = "0";
        } else if (document.getElementById("userList").style.right == "0px")
          document.getElementById("userList").style.right = "-100%";
      } else if (dir == "left") {
        if (document.getElementById("userList"))
          if ($("#userList").css("right") != "0px")
            document.getElementById("userList").style.right = "0";
      }
    });
    this.id = this.$route.params.id;
    this.messages = this.joinedGroupList.filter(e => e.groupId == this.id)[0] || {};

  }
};
</script>

<style scoped>
hr {
  width: 100%;
}
.show {
  display: block !important;
}
.image_preview {
  /* width: 100%; */
  height: 100%;
  background: no-repeat center;
  background-size: cover;
  position: relative;
  z-index: 3;
  min-width: 100px;
}
.images-preview {
  position: absolute;
  top: -100px;
  left: 0;
  right: 0;
  bottom: -100px;
  height: 100px;
  z-index: 2;
  overflow: auto hidden;
}
.image_preview:hover i {
  display: block !important;
}
.user-image {
  height: 30px;
  width: 30px;
}
hr {
  width: 100%;
}
* {
  margin-right: 0 !important;
}
